<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>文章列表</span>
      </div>
      <!-- 搜索区域 -->
      <div class="search-box">
        <el-form :inline="true" :model="q">
          <el-form-item label="文章分类">
            <el-select
              v-model="selCateId"
              placeholder="请选择分类"
              size="small"
            >
              <el-option
                v-for="item in cateList"
                :key="item.id"
                :label="item.cate_name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="发布状态" style="margin-left: 15px">
            <el-select v-model="state" placeholder="请选择状态" size="small">
              <el-option label="已发布" value="已发布"></el-option>
              <el-option label="草稿" value="草稿"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button @click="filterArtList" type="primary" size="small"
              >筛选</el-button
            >
            <el-button @click="resetArtList" type="info" size="small"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
        <!-- 发表文章的按钮 -->
        <el-button
          @click="pubVisible = true"
          type="primary"
          size="small"
          class="btn-pub"
          >发表文章</el-button
        >
      </div>

      <!-- 文章表格区域 -->
      <el-table :data="artList" style="width: 100%">
        <el-table-column label="文章标题">
          <template v-slot="scope">
            <el-link @click="showDetails(scope.row.id)" type="primary">{{
              scope.row.title
            }}</el-link>
          </template>
        </el-table-column>
        <el-table-column prop="cate_name" label="分类"> </el-table-column>
        <el-table-column label="发表时间">
          <template v-slot="scope">
            {{ formatDate(scope.row.pub_date) }}
          </template>
        </el-table-column>
        <el-table-column prop="state" label="状态"> </el-table-column>
        <el-table-column label="操作">
          <template v-slot="scope">
            <el-button @click="delArticle(scope.row.id)" size="mini" type="danger"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="sizeChangeHandler"
        @current-change="currentChangeHandler"
        :current-page="q.pagenum"
        :page-sizes="[2, 3, 5, 10]"
        :page-size="q.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <el-dialog
      fullscreen
      title="发表文章"
      :visible.sync="pubVisible"
      width="30%"
      :before-close="beforeCloseHandler"
      @closed="pubClosedHandler"
    >
      <el-form
        :model="artForm"
        :rules="artRules"
        ref="artForm"
        label-width="100px"
      >
        <el-form-item label="文章标题" prop="title">
          <el-input
            placeholder="请输入标题"
            v-model.trim="artForm.title"
          ></el-input>
        </el-form-item>
        <el-form-item label="文章分类" prop="cate_id">
          <el-select v-model="artForm.cate_id" placeholder="请选择分类">
            <el-option
              v-for="item in cateList"
              :key="item.id"
              :label="item.cate_name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="文章内容" prop="content">

          <quill-editor
            @change="$refs.artForm.clearValidate('content')"
            v-model="artForm.content"
          ></quill-editor>
        </el-form-item>
        <el-form-item label="文章封面" prop="cover_img">

          <img :src="coverPreview" alt="" class="cover-img" ref="imgRef" />
          <br />
          <!-- 文件选择框，默认被隐藏 -->
          <input
            v-if="pubVisible"
            type="file"
            style="display: none"
            accept="image/*"
            ref="iptFileRef"
            @change="coverChangeHandler"
          />
          <!-- 选择封面的按钮 -->
          <el-button @click="$refs.iptFileRef.click()" type="text"
            >+ 选择封面</el-button
          >
        </el-form-item>
        <el-form-item prop="state">
          <el-button @click="pubArticle('已发布')" type="primary"
            >发布</el-button
          >
          <el-button @click="pubArticle('草稿')" type="info"
            >存为草稿</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>

    <!-- 文章详情 dialog -->
    <el-dialog title="文章预览" :visible.sync="detailsVisible" width="80%">
      <h1 class="title">{{ artDetails.title }}</h1>
      <div class="info">
        <span>作者: {{ artDetails.nickname || artDetails.username }}</span>
        <span>发布时间: {{ formatDate(artDetails.pub_date) }}</span>
        <span>所属分类: {{ artDetails.cate_name }}</span>
        <span>状态: {{ artDetails.state }}</span>
      </div>

      <!-- 分割线 -->
      <el-divider></el-divider>

      <!-- 文章封面 -->
      <img
        :src="'http://www.liulongbin.top:3008' + artDetails.cover_img"
        alt=""
      />

      <!-- 文章详情 -->
      <div class="detail-box" v-html="artDetails.content"></div>
    </el-dialog>
  </div>
</template>

<script>
// 导入默认图片 ../../../assets/images/cover.jpg
import defaultImg from '@/assets/images/cover.jpg'
import dayjs from 'dayjs'

export default {
  name: 'ArtList',
  created () {
    // 获取分类列表
    this.getArtCate()
    // 获取文章列表
    this.getArtList()
  },
  data () {
    return {
      // 查询参数对象
      q: {
        pagenum: 1,
        pagesize: 2,
        cate_id: '',
        state: ''
      },
      selCateId: '',
      state: '',
      pubVisible: false,
      artForm: {
        title: '',
        cate_id: '',
        content: '',
        cover_img: null,
        state: ''
      },
      // 封面预览
      coverPreview: defaultImg,
      artRules: {
        title: [
          { required: true, message: '请输入文章标题', trigger: 'blur' },
          {
            min: 1,
            max: 30,
            message: '文章标题必须是1-30个字符',
            trigger: 'blur'
          }
        ],
        cate_id: [
          { required: true, message: '请输选择文章分类', trigger: 'change' }
        ],
        // 不能提醒用户, 但可以做兜底校验
        content: [
          { required: true, message: '请输入文章内容', trigger: 'blur' },
          { pattern: /^\S{1,}$/, message: '请输入文章内容', trigger: 'blur' }
        ],
        // 不能提醒用户, 但可以做兜底校验
        cover_img: [
          { required: true, message: '请选择文章封面', trigger: 'blur' }
        ]
      },
      cateList: [],
      artList: [],
      total: 0,
      artDetails: {},
      detailsVisible: false
    }
  },
  methods: {
    // done: 用于关闭 dialog 的函数, before-close 会暂停 dialog 的关闭
    async beforeCloseHandler (done) {
      // console.log('用户想关我!!!')
      const result = await this.$confirm(
        '真的要关闭吗宝!',
        '提示'
      ).catch((e) => e)
      // 如果用户点击确认, 打印 confirm, 如果点击取消, 打印 cancel
      // console.log(result)
      // if (result === 'confirm') this.pubVisible = false
      if (result === 'confirm') done()
    },
    async getArtCate () {
      // 发送请求获取所有分类信息
      const { data: res } = await this.$http.get('/my/cate/list')
      // console.log(res.data)
      // 将获取到的分类数据赋值到 data 中
      if (res.code === 0) this.cateList = res.data
    },
    coverChangeHandler (e) {
      // 当用户选择图片时判断是否选择了图片
      // console.log(e.target.files)
      const files = e.target.files
      if (files.length === 0) {
        // 如果没选择图片, 将 cover_img 清空
        this.artForm.cover_img = null
        // 在 js 中使用图片需要先导入
        // DOM 操作  不建议
        // this.$refs.imgRef.setAttribute('src', defaultImg)
        this.coverPreview = defaultImg
      } else {
        this.artForm.cover_img = files[0]
        // 将图片转成 url, 给 img 标签显示
        const url = URL.createObjectURL(files[0])
        // this.$refs.imgRef.setAttribute('src', url)
        this.coverPreview = url
        // console.log(url)

        // 扩展: 如果选择了图片 移除表单校验的结果
        this.$refs.artForm.clearValidate('cover_img')
      }
    },
    pubArticle (state) {
      this.artForm.state = state

      this.$refs.artForm.validate(async (result) => {
        if (!result) return

        const fd = new FormData()

        Object.keys(this.artForm).forEach((k) => fd.append(k, this.artForm[k]))

        // 3. 发送请求发布文章
        const { data: res } = await this.$http.post('/my/article/add', fd)
        // console.log(res)
        if (res.code === 0) {
          // 4. 如果发布文章成功, 关闭 dialog
          this.pubVisible = false
          // 5. 刷新列表数据
          this.getArtList()
        }
      })
    },
    pubClosedHandler () {
      // 清空表单数据
      this.$refs.artForm.resetFields()
      // 预览图还原成默认图片
      this.coverPreview = defaultImg
    },
    async getArtList () {
      // 发请求获取文章列表数据
      const { data: res } = await this.$http.get('/my/article/list', {
        params: this.q
      })
      if (res.code === 0) {
        // console.log(res.total)
        this.artList = res.data
        // 获取到所有数据总数, 赋值给 data 的 total , 分页组件中使用
        this.total = res.total
      }
    },
    formatDate (date) {
      return dayjs(date).format('YYYY-MM-DD HH:mm:ss')
    },
    sizeChangeHandler (size) {
      // console.log(this.q)
      // 亲测: 组件内部会自动帮我们修改 pagenum 为 1
      // this.q.pagenum = 1
      this.q.pagesize = size
      this.getArtList()
    },
    currentChangeHandler (cur) {
      // console.log('当前页发生变化了')
      // cur: 当前页 currentPage
      // console.log(cur)
      // 在页面发生变化时, 将当前页赋值给 q.pagenum, 重新发请求即可
      this.q.pagenum = cur
      this.getArtList()
    },
    filterArtList () {
      // 手动将 q.pagenum 改为 1 , 避免页码值大于筛选后的数据
      this.q.pagenum = 1
      // 当用户点击筛选时 手动把绑定的数据赋值给 q 的属性
      this.q.cate_id = this.selCateId
      this.q.state = this.state
      this.getArtList()
    },
    resetArtList () {
      // 将 q 赋值为默认参数后发请求
      this.q = {
        pagenum: 1,
        pagesize: 2,
        cate_id: '',
        state: ''
      }
      this.getArtList()
    },
    async showDetails (id) {
      // console.log(id)
      // 1. 当用户点击时传入 id 发送请求
      const { data: res } = await this.$http.get('/my/article/info', {
        params: { id }
      })
      if (res.code === 0) {
        // console.log(res)
        // 2. 获取到文章详情时将数据存储到 data 中
        this.artDetails = res.data
        // 3. 显示详情的对话框
        this.detailsVisible = true
      }
    },
    async delArticle (id) {
      // console.log(id)
      // 1. 点击事件传入 id
      // 2. 删除前提醒用户
      const result = await this.$confirm('真的要删除吗宝', '提示框').catch(e => e)
      if (result === 'cancel') return
      // 3. 发请求让服务器删除数据
      const { data: res } = await this.$http.delete('/my/article/info', {
        params: { id }
      })
      // 4. 重新获取最新的数据
      if (res.code !== 0) return

      // console.log(res)
      if (this.artList.length === 1 && this.q.pagenum > 1) {
        this.q.pagenum--
      }
      this.getArtList()
    }
  }
}
</script>

<style lang="less" scoped>
.search-box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  .btn-pub {
    margin-top: 5px;
  }
}

.el-select {
  width: 100%;
}

/deep/ .ql-editor {
  min-height: 300px;
}

// 设置图片封面的宽高
.cover-img {
  width: 400px;
  height: 280px;
  object-fit: cover;
}

.title {
  font-size: 24px;
  text-align: center;
  font-weight: normal;
  color: #000;
  margin: 0 0 10px 0;
}

.info {
  font-size: 12px;
  span {
    margin-right: 20px;
  }
}

// 修改 dialog 内部元素的样式，需要添加样式穿透
/deep/ .detail-box {
  img {
    width: 500px;
  }
}
</style>
